import React, { Component, lazy, Suspense } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import { reduxForm, reset } from "redux-form";
import { getListMutasiCiok } from "../actions/mutasiCiokAction";

import ReactTooltip from "react-tooltip";
import Swal from "sweetalert2";
import { connect } from "react-redux";
import { getUUID } from "../actions/fakturAction";
import {
  finishSend,
  getBruto,
  getNetto,
  getQty,
  progressSend,
} from "../actions/LogicAction";
import Axios from "axios";
import PrintNota from "./Component/PrintNota";
import { warning } from "../Alert";
import { deleteIndex } from "../deleteLocalStorage";
import Skeleton from "react-loading-skeleton";
import HeaderCard from "../HeaderCard";

const FormHeadMutasiCiok = lazy(() => import("./Component/FormHeadMutasiCiok"));
const FormMutasiCiok = lazy(() => import("./Component/FormMutasiCiok"));
const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);

const maptostate = (state) => {
  return {
    listBarang: state.provinsi.mutasiCiok,
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumQty: state.perhitungan.sumQty,
    onSend: state.provinsi.onSend,
  };
};

const server = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class MutasiCiok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
          footer: "",
        },
        {
          dataField: "kadarTransaksi",
          text: "Kat Harga",
          footer: "Total Bruto",
        },
        {
          dataField: "bruto",
          text: "Bruto (Gr)",
          footer: (columnData) =>
            columnData
              .reduce((acc, item) => parseFloat(acc) + parseFloat(item), 0)
              .toFixed(3),
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga",
          footer: "Total Netto",
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          footer: (columnData) =>
            columnData
              .reduce((acc, item) => parseFloat(acc) + parseFloat(item), 0)
              .toFixed(3),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "mutasiCiok",
                      rowIndex,
                      this.props.dispatch,
                      getListMutasiCiok()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => console.log("EDIT")}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
    };
  }

  handleSubmit(data) {
    if (data.jenisMutasi === undefined) {
      warning("Silahkan Pilih jenis Mutasi");
      return false;
    } else if (data.keterangan === undefined) {
      warning("Silahkan isi keterangan");
      return false;
    } else if (data.noBon === undefined) {
      warning("Silahkan isi Nomor Bon");
      return false;
    } else if (data.tanggal === undefined) {
      warning("Silahkan isi tanggal");
      return false;
    } else if (data.transaksi === undefined) {
      warning("Silahkan pilih jenis Transaksi");
      return false;
    } else if (localStorage.getItem("mutasiCiok") === null) {
      warning("Mohon Tambahkan Barang Yang akan dipindahkan");
      return false;
    }
    var simpan = {
      noFaktur: data.noMutasi.toUpperCase(),
      tanggal: data.tanggal.toUpperCase(),
      kategoriTransaksi: data.transaksi.toUpperCase(),
      noBon: data.noBon.toUpperCase(),
      kodeLokasi: "PUSAT",
      keterangan: data.keterangan.toUpperCase(),
      tipeFaktur: data.jenisMutasi.toUpperCase(),
      barangDetails: JSON.parse(localStorage.getItem("mutasiCiok")),
      footerItem: {
        totalP: 0,
        totalK: 0,
        totalBruto: this.props.sumBruto,
        totalNetto: this.props.sumNetto,
        totalNettoExt: 0,
        totalQty: this.props.sumQty,
        totalpkg: 0,
        totalGross: 0,
        totalRp: 0,
      },
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("mutasiCiok"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        i++,
        ticket.kodeJenis,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.netto,
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    console.log(tableRows);
    var columTable = ["No", "Jenis", "Bruto", "kadar (%)", "Netto"];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(server + "item/dc/mutasilk", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal.toUpperCase(),
              // "",
              "Keterangan",
              data.keterangan.toUpperCase(),
              // "",
              "No Pindah",
              this.props.noFaktur.toUpperCase(),
              // "",
              "",
              "",
              "ADMIN",
              data.tanggal.toUpperCase(),
              "",
              columTable,
              "MUTASI BARANG",
              tableRows
            )
          )
          .then(() => localStorage.removeItem("mutasiCiok"))
          .then(() => this.props.dispatch(reset("FormHeadMutasiCiok")))
          .then(() => this.props.dispatch(getListMutasiCiok()))
          .then(() => this.props.dispatch(getUUID()))
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Sepertinya ada kendala, silahkan cek koneksi",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => console.log(err))
          .then(() => this.props.dispatch(finishSend()))
      );
  }
  handleSubmitModal(data) {
    var barang = data.kodeBarang ? data.kodeBarang : "DEFAULT|DEFAULT|DEFAULT";
    var split = barang.split("|");
    var array = JSON.parse(localStorage.getItem("mutasiCiok")) || [];
    var simpan = {
      kadarTransaksi: parseFloat(data.kadarTransaksi).toFixed(3),
      qty: 1,
      kodeJenis: split[0],
      p: 0,
      pkg: 0,
      kodeKategori: "-",
      noBarcode: "-",
      k: 0,
      discGlobalPr: 0,
      kadarCetak: parseFloat(split[1]).toFixed(3),
      gross: 0,
      nettoDiscGlobal: 0,
      discGlobalGr: 0,
      nettoDiscGlobalExt: 0,
      noFaktur: this.props.noFaktur.toUpperCase(),
      bruto: parseFloat(data.bruto).toFixed(3),
      netto: parseFloat(data.netto).toFixed(3),
      keterangan: data.keterangan.toUpperCase(),
      rp: 0,
    };
    array.push(simpan);
    localStorage.setItem("mutasiCiok", JSON.stringify(array));
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("FormMutasiCiok")))
      .then(() => this.props.dispatch(getListMutasiCiok()))
      .then(() => this.props.dispatch(finishSend()));
  }

  componentDidMount() {
    this.props.dispatch(getListMutasiCiok());
    this.props.dispatch(getUUID());
    this.props.dispatch(getBruto("mutasiCiok"));
    this.props.dispatch(getNetto("mutasiCiok"));
    this.props.dispatch(getQty("mutasiCiok"));
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Mutasi Ciok & Rosok" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    <Suspense
                      fallback={
                        <Skeleton width={"100%"} height={50} count={5} />
                      }
                    >
                      <FormMutasiCiok
                        onSubmit={(data) => this.handleSubmitModal(data)}
                        noFaktur={this.props.noFaktur}
                      />
                    </Suspense>
                  }
                  title="Tambah Data Mutasi Ciok"
                />
                <div className="row">
                  <Suspense
                    fallback={<Skeleton width={"100%"} height={50} count={5} />}
                  >
                    <FormHeadMutasiCiok
                      onSubmit={(data) => this.handleSubmit(data)}
                      noFaktur={this.props.noFaktur}
                      onSend={this.props.onSend}
                    />
                  </Suspense>
                  <div className="col-md-12 col-lg-12">
                    {this.props.listBarang ? (
                      <BootstrapTable
                        keyField="id"
                        data={this.props.listBarang}
                        columns={this.state.columns}
                        bordered={true}
                        headerClasses="header-table"
                      />
                    ) : (
                      <BootstrapTable
                        keyField="id"
                        data={[]}
                        columns={this.state.columns}
                        bordered={true}
                        headerClasses="header-table"
                        noDataIndication="Tidak Ada Data"
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Mutasi Barang
                </h4>
                <button
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

MutasiCiok = reduxForm({
  form: "MutasiCiok",
  enableReinitialize: true,
})(MutasiCiok);
export default connect(maptostate, null)(MutasiCiok);
